<template>
  <div class="text-main">
    <!--  -->
    <Alert></Alert>
    <!--  -->
    <loading :active.sync="isLoading"></loading>
    <form class="form-signin" @submit.prevent="signin">
      <img
        class="mb-4"
        src="../assets/images/Dry Flower-logo/profile.png"
        alt
        width="300"
        height="300"
      />
      <h1 class="h3 mb-3 font-weight-normal text-center"></h1>
      <label for="inputEmail" class="sr-only"></label>
      <input
        type="email"
        id="inputEmail"
        class="form-control"
        placeholder="Email address"
        required
        autofocus
        v-model="user.username"
      />
      <label for="inputPassword" class="sr-only"></label>
      <input
        type="password"
        id="inputPassword"
        class="form-control"
        placeholder="Password"
        required
        v-model="user.password"
      />
      <button class="btn btn-lg btn-main btn-block" type="submit"></button>
      <p class="mt-5 mb-3 text-muted">&copy; 2017-2019</p>
    </form>
  </div>
</template>

<script>
// 
import Alert from '../components/AlerMessage.vue';

export default {
  name: 'Login',
  components: {
    Alert,
  },
  data() {
    return {
      user: {
        username: '',
        password: '',
      },
      isLoading: false,
    };
  },
  methods: {
    signin() {
      //  API  /signin
      //  /admin/signin
      const api = `${process.env.VUE_APP_APIPATH}/admin/signin`;
      const vm = this;
      vm.isLoading = true;
      //  API  post
      this.$http.post(api, vm.user).then((response) => {
        //  response.data.success  true 
        if (response.data.success) {
          vm.$router.push('/admin/products');
          vm.isLoading = false;
        } else {
          vm.$bus.$emit('message:push', response.data.message, 'danger');
          vm.isLoading = false;
        }
      });
    },
  },
};
</script>

