              class="section-img w-100"
            />
          </div>
          <div
            class="col-md-6 text-forText d-flex justify-content-end pr-3 mr-md-0 mr-4 animatedY"
          >
            <div class="vertical-text py-3 section-line-height h6">
              <p></p>
