      </div>
      <!--  -->
      <div class="row mt-4">
        <div class="col-md-4 mb-4 card-position" v-for="item in randomProducts" :key="item.id">
          <div class="card border-0 shadow-sm" @click.prevent="getProductID(item.id)">
            <a href="#">
