              <p class="card-text">{{ item.content }}</p>
            </div>
            <div class="card-footer d-flex bg-white border-0">
              <div class="d-flex flex-column">
                <!--  -->
                <div class="h5" v-if="!item.price"> {{ item.origin_price }} </div>
                <!--  -->
                <!-- 0''nan  false -->
                <del class="h6 text-secondary" v-if="item.price"> {{ item.origin_price }} </del>
                <div class="h5 text-sub" v-if="item.price"> {{ item.price }} </div>
              </div>
            </div>
          </div>
          <button
            type="button"
            class="btn btn-outline-third ml-auto card-cart"
            @click="addtoCart(item.id)"
          >
            <i class="fas fa-spinner fa-spin" v-if="item.id === status.loadingItem"></i>
            <i class="fas fa-shopping-cart" v-else></i>
          </button>
          <div class="heart-position">
            <button
              type="button"
              class="btn text-danger"
              @click.prevent="removeLove(item)"
              v-if="changeLove(item)"
            >
              <i class="fas fa-heart fa-2x"></i>
            </button>
            <button type="button" class="btn text-danger" @click.prevent="addLove(item)" v-else>
              <i class="far fa-heart fa-2x"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
    <Footer />
    <ShoppingCart
      :cart-data="cartItem"
      :loading-img="status"
      @opencart="getCart(1)"
      @removecart="removeCartItem"
    ></ShoppingCart>
    <FavoriteList
      :favorite-data="favorite"
      :loading-img="status"
      @openfavorite="openFavoriteList(1)"
      @removefavorite="removeLove"
      @favoriteToCart="addtoCart"
    ></FavoriteList>
  </div>
</template>

<script>
import $ from 'jquery';
import NavBar from '../components/NavBar.vue';
import Footer from '../components/Footer.vue';
import ShoppingCart from '../components/ShoppingCart.vue';
import FavoriteList from '../components/FavoriteList.vue';

export default {
  name: 'Index',
  components: {
    NavBar,
    Footer,
    ShoppingCart,
    FavoriteList,
  },
  data() {
    return {
      // 
      products: [],
      randomProducts: [],
      // 
      product: {},
      // 
      cartItem: [],
      // 
      favorite: [],
      // 
      status: {
        loadingItem: '',
      },
      isLoading: false,
      scrollMsg:
        '  2020/11/20  costdown   ',
      intervalId: null, // data id
      sliceIndex: '',
    };
  },
  methods: {
    // 
    getProducts() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products/all`;
      // vm.isLoading = true;
      this.$http.get(api).then((response) => {
        // 
        response.data.products.forEach((item) => {
          if (item.is_enabled === 1) {
            vm.products.push(item);
          }
        });
        vm.randomProducts = vm.getRandomProducts(vm.products, 3);
      });
    },
    // 
    getRandomProducts(arr, count) {
      const shuffled = arr.slice(0);
      let i = Number(arr.length);
      const min = i - count;
      let temp;
      let index;
      while (i - min < 1) {
        i -= min;
        index = Math.floor((i + 1) * Math.random());
        temp = shuffled[index];
        shuffled[index] = shuffled[i];
        shuffled[i] = temp;
      }
      return shuffled.slice(min);
    },
    // 
    getProductID(id) {
      const vm = this;
      vm.$router.push(`/detail/${id}`);
    },
    // 
    getCart(open) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      if (open === 1) {
        $('.cart-dropdown').toggleClass('show');
      }
      this.$http.get(api).then((response) => {
        vm.cartItem = response.data.data;
      });
    },
    //  dropmenu 
    toggleCart() {
      $(document).on('click', (e) => {
        //  .cart-dropdown  .cart-dropdown  .show 
        //  .cart-dropdown  .show 
        if (
          !e.target.closest('.cart-dropdown')
          && $('.cart-dropdown').hasClass('show')
        ) {
          $('.cart-dropdown').removeClass('show');
        }
      });
    },
    // 
    addtoCart(id, qty = 1) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      vm.isLoading = true;
      //  ID  loadingItem
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        //  qty 
        qty,
      };
      // {data: }
      this.$http
        .post(api, {
          data: cart,
        })
        .then(() => {
          // Modal  loadingItem 
          vm.status.loadingItem = '';
          // 
          vm.isLoading = false;
          vm.getCart();
        });
    },
    // 
    removeCartItem(id) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
      vm.status.loadingItem = id;
      vm.isLoading = true;
      this.$http.delete(api).then(() => {
        vm.status.loadingItem = '';
        vm.isLoading = false;
        // 
        vm.getCart();
      });
    },
    // 
    lang() {
      const vm = this;
      if (vm.intervalId != null) return;
      vm.intervalId = setInterval(() => {
        // 
        const start = vm.scrollMsg.substring(0, 1);
        // 
        const end = vm.scrollMsg.substring(1);
        // 
        vm.scrollMsg = end + start;
      }, 100);
    },
    // 
    stop() {
      const vm = this;
      clearInterval(vm.intervalId);
      // intervalIdnull
      vm.intervalId = null;
    },
    // 
    getfavorite() {
      const vm = this;
      vm.favorite = JSON.parse(localStorage.getItem('savefavorite')) || [];
    },
    // 
    addLove(item) {
      const vm = this;
      if (!vm.favorite.includes(item)) {
        vm.favorite.push(item);
        // vm.stared  localStorage
        localStorage.setItem('savefavorite', JSON.stringify(vm.favorite));
      }
    },
    // 
    changeLove(item) {
      const vm = this;
      return vm.favorite.some((el) => {
        const result = item.id === el.id;
        return result;
      });
    },
    // 
    removeLove(favoriteItem) {
      const vm = this;
      //  index
      vm.favorite.forEach((item, key) => {
        if (favoriteItem.id === item.id) {
          vm.sliceIndex = key;
        }
      });
      //  toggle  dropdown
      // 
      setTimeout(() => vm.favorite.splice(vm.sliceIndex, 1), 10);
      // vm.stared  localStorage
      setTimeout(
        () => localStorage.setItem('savefavorite', JSON.stringify(vm.favorite)),
        10,
      );
    },
    // 
    openFavoriteList(open) {
      if (open === 1) {
        $('.favorite-dropdown').toggleClass('show');
      }
    },
    // 
    toggleFavorite() {
      $(document).on('click', (e) => {
        //  .cart-dropdown  .cart-dropdown  .show 
        //  .cart-dropdown  .show 
        if (
          !e.target.closest('.favorite-dropdown')
          && $('.favorite-dropdown').hasClass('show')
        ) {
          $('.favorite-dropdown').removeClass('show');
        }
      });
    },
    sectionFadeIn() {
      $(window).scroll(() => {
        //  scrollPos = window 
        const scrollPos = $(window).scrollTop();
        //  windowHeight =  window 
        const windowHeight = $(window).height();
        $('.animatedY').each(function animatedY() {
          //  thisPos = animated 
          const thisPos = $(this).offset().top;
          // console.log('animated', thisPos);
          if (thisPos <= scrollPos + windowHeight / 1.5) {
            // console.log('animated', thisPos);
            $(this).addClass('fadeInY');
          }
        });
        $('.animatedLR').each(function animatedLR() {
          //  thisPos = animated 
          const thisPos = $(this).offset().top;
          // console.log('animated', thisPos);
          if (thisPos <= scrollPos + windowHeight / 1.2) {
            // console.log('animated', thisPos);
            $(this).addClass('fadeInLR');
          }
        });
      });
    },
  },
  created() {
    this.getProducts();
    this.getCart();
    this.lang();
    this.getfavorite();
  },
  mounted() {
    this.toggleCart();
    this.toggleFavorite();
    this.sectionFadeIn();
  },
};
</script>
