<script>
import $ from 'jquery';
import NavBar from '../components/NavBar.vue';
import Footer from '../components/Footer.vue';
import ShoppingCart from '../components/ShoppingCart.vue';
import FavoriteList from '../components/FavoriteList.vue';

export default {
  name: 'Productlist',
  components: {
    NavBar,
    Footer,
    ShoppingCart,
    FavoriteList,
  },
  data() {
    return {
      // 
      products: [],
      sortProduct: [],
      // 
      cartItem: [],
      // 
      favorite: [],
      status: {
        loadingItem: '',
      },
      isLoading: false,
      filterText: '',
      sliceIndex: '',
    };
  },
  methods: {
    // 
    getProducts() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products/all`;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        // 
        response.data.products.forEach((item) => {
          if (item.is_enabled === 1) {
            vm.products.push(item);
          }
          vm.isLoading = false;
        });
      });
    },
    // 
    getProductID(id) {
      const vm = this;
      vm.$router.push(`/detail/${id}`);
    },
    getSort(tag) {
      const vm = this;
      vm.sortProduct = vm.products.filter((item) => {
        if (item.category === tag) {
          return true;
        }
        return false;
      });
    },
    // 
    getCart(open) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      if (open === 1) {
        $('.cart-dropdown').toggleClass('show');
      }
      this.$http.get(api).then((response) => {
        vm.cartItem = response.data.data;
      });
    },
    //  dropmenu 
    toggleCart() {
      $(document).on('click', (e) => {
        //  .cart-dropdown  .cart-dropdown  .show 
        //  .cart-dropdown  .show 
        if (
          !e.target.closest('.cart-dropdown')
          && $('.cart-dropdown').hasClass('show')
        ) {
          $('.cart-dropdown').removeClass('show');
        }
        // if (e.target.id != 'cart-id') {
        //   $(".cart-menu").toggleClass('show');
        // }
      });
    },
    // 
    addtoCart(id, qty = 1) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      vm.isLoading = true;
      //  ID  loadingItem
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        //  qty 
        qty,
      };
      // {data: }
      this.$http
        .post(api, {
          data: cart,
        })
        .then(() => {
          // Modal  loadingItem 
          vm.status.loadingItem = '';
          vm.isLoading = false;
          // 
          vm.getCart();
        });
    },
    // 
    removeCartItem(id) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      vm.status.loadingItem = id;
      this.$http.delete(api).then(() => {
        vm.status.loadingItem = '';
        vm.isLoading = false;
        // 
        vm.getCart();
      });
    },
    // 
    getfavorite() {
      const vm = this;
      vm.favorite = JSON.parse(localStorage.getItem('savefavorite')) || [];
    },
    // 
    addLove(item) {
      const vm = this;
      if (!vm.favorite.includes(item)) {
        vm.favorite.push(item);
        // vm.stared  localStorage
        localStorage.setItem('savefavorite', JSON.stringify(vm.favorite));
      }
    },
    // 
    changeLove(item) {
      const vm = this;
      return vm.favorite.some((el) => {
        const result = item.id === el.id;
        return result;
      });
    },
    // 
    removeLove(favoriteItem) {
      const vm = this;
      //  index
      vm.favorite.forEach((item, key) => {
        if (favoriteItem.id === item.id) {
          vm.sliceIndex = key;
        }
      });
      vm.favorite.splice(vm.sliceIndex, 1);
      // vm.stared  localStorage
      localStorage.setItem('savefavorite', JSON.stringify(vm.favorite));
    },
    // 
    openFavoriteList(open) {
      if (open === 1) {
        $('.favorite-dropdown').toggleClass('show');
      }
    },
    // 
    toggleFavorite() {
      $(document).on('click', (e) => {
        //  .cart-dropdown  .cart-dropdown  .show 
        //  .cart-dropdown  .show 
        if (
          !e.target.closest('.favorite-dropdown')
          && $('.favorite-dropdown').hasClass('show')
        ) {
          $('.favorite-dropdown').removeClass('show');
        }
      });
    },
  },
  computed: {
    filterArrayALL() {
      const vm = this;
      //  return  (item.name  vm.filterText)
      //  arrayData  ""  return  filterArray
      return vm.products.filter(item => item.title.match(vm.filterText));
    },
    filterArraySort() {
      const vm = this;
      //  return  (item.name  vm.filterText)
      //  arrayData  ""  return  filterArray
      return vm.sortProduct.filter(item => item.title.match(vm.filterText));
    },
  },
  created() {
    this.getProducts();
    this.getCart();
    this.getfavorite();
  },
  mounted() {
    this.toggleCart();
    this.toggleFavorite();
  },
};
</script>
