<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <header>
      <NavBar />
    </header>
    <div class="container py-3">
      <div class="row justify-content-center">
        <div class="col-md-10">
          <div class="row">
            <div class="col-md-6 text-center">
              <img :src="product.imageUrl" class="img-detail img-thumbnail" alt />
            </div>
            <div class="col-md-6">
              <div class="d-flex align-items-center">
                <h2 class="text-main mb-0">{{ product.title }}</h2>
                <a
                  href="#"
                  class="btn text-danger"
                  @click.prevent="removeLove(product)"
                  v-if="changeLove(product)"
                >
                  <i class="fas fa-heart fa-2x"></i>
                </a>
                <a href="#" class="btn text-danger" @click.prevent="addLove(product)" v-else>
                  <i class="far fa-heart fa-2x"></i>
                </a>
              </div>
              <blockquote class="blockquote mt-3">
                <p class="mb-0">{{ product.content }}</p>
                <footer class="blockquote-footer text-right">{{ product.description }}</footer>
              </blockquote>
              <div class="d-flex justify-content-between align-items-baseline">
                <div class="h4" v-if="!product.price">{{ product.origin_price }} </div>
                <del class="h6 text-secondary" v-if="product.price">
                   {{ product.origin_price }} </del>
                <div class="h4 text-sub" v-if="product.price"> {{ product.price }} </div>
              </div>
              <select name class="form-control mt-3" v-model="product.num">
                <option :value="num" v-for="num in 10" :key="num">
                   {{num}} {{product.unit}}</option>
              </select>
              <button
                type="button"
                class="btn btn-outline-third mt-3 d-flex ml-auto align-items-center"
                @click="addtoCart(product.id, product.num)"
              >
                <i class="fas fa-spinner fa-spin fa-fw"
                v-if="product.id === status.loadingItem"></i>
                <i class="fas fa-shopping-cart fa-fw" v-else></i> 
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer />
    <ShoppingCart
      :cart-data="cartItem"
      :loading-img="status"
      @opencart="getCart(1)"
      @removecart="removeCartItem"
    ></ShoppingCart>
    <FavoriteList
      :favorite-data="favorite"
      :loading-img="status"
      @openfavorite="openFavoriteList(1)"
      @removefavorite="removeLove"
      @favoriteToCart="addtoCart"
    ></FavoriteList>
  </div>
</template>

<script>
import $ from 'jquery';
import NavBar from '../components/NavBar.vue';
import Footer from '../components/Footer.vue';
import ShoppingCart from '../components/ShoppingCart.vue';
import FavoriteList from '../components/FavoriteList.vue';

export default {
  data() {
    return {
      productId: '',
      product: {},
      // 
      cartItem: [],
      // 
      favorite: [],
      isLoading: false,
      status: {
        loadingItem: '',
      },
      sliceIndex: '',
    };
  },
  components: {
    NavBar,
    Footer,
    ShoppingCart,
    FavoriteList,
  },
  methods: {
    getProduct(id) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.product = response.data.product;
        //  1
        vm.product.num = 1;
        vm.isLoading = false;
      });
    },
    //     qty = 1 qty  1
    addtoCart(id, qty = 1) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      vm.isLoading = true;
      //  ID  loadingItem
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        //  qty 
        qty,
      };
      // {data: }
      this.$http
        .post(api, {
          data: cart,
        })
        .then(() => {
          // Modal  loadingItem 
          vm.status.loadingItem = '';
          // 
          vm.isLoading = false;
          vm.getCart();
        });
    },
    // 
    getCart(open) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      if (open === 1) {
        $('.cart-dropdown').toggleClass('show');
      }
      this.$http.get(api).then((response) => {
        vm.cartItem = response.data.data;
      });
    },
    //  dropmenu 
    toggleCart() {
      $(document).on('click', (e) => {
        //  .cart-dropdown  .cart-dropdown  .show 
        //  .cart-dropdown  .show 
        if (
          !e.target.closest('.cart-dropdown')
          && $('.cart-dropdown').hasClass('show')
        ) {
          $('.cart-dropdown').removeClass('show');
        }
        // if (e.target.id != 'cart-id') {
        //   $(".cart-menu").toggleClass('show');
        // }
      });
    },
    // 
    removeCartItem(id) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      vm.status.loadingItem = id;
      this.$http.delete(api).then(() => {
        vm.status.loadingItem = '';
        vm.isLoading = false;
        // 
        vm.getCart();
      });
    }, // 
    getfavorite() {
      const vm = this;
      vm.favorite = JSON.parse(localStorage.getItem('savefavorite')) || [];
    },
    // 
    addLove(item) {
      const vm = this;
      if (!vm.favorite.includes(item)) {
        vm.favorite.push(item);
        // vm.stared  localStorage
        localStorage.setItem('savefavorite', JSON.stringify(vm.favorite));
      }
    },
    // 
    changeLove(item) {
      const vm = this;
      return vm.favorite.some((el) => {
        const result = item.id === el.id;
        return result;
      });
    },
    // 
    removeLove(favoriteItem) {
      const vm = this;
      //  index
      vm.favorite.forEach((item, key) => {
        if (favoriteItem.id === item.id) {
          vm.sliceIndex = key;
        }
      });
      vm.favorite.splice(vm.sliceIndex, 1);
      // vm.stared  localStorage
      localStorage.setItem('savefavorite', JSON.stringify(vm.favorite));
    },
    // 
    openFavoriteList(open) {
      if (open === 1) {
        $('.favorite-dropdown').toggleClass('show');
      }
    },
    // 
    toggleFavorite() {
      $(document).on('click', (e) => {
        //  .cart-dropdown  .cart-dropdown  .show 
        //  .cart-dropdown  .show 
        if (
          !e.target.closest('.favorite-dropdown')
          && $('.favorite-dropdown').hasClass('show')
        ) {
          $('.favorite-dropdown').removeClass('show');
        }
      });
    },
  },
  created() {
    //  ID
    // orderId  index.js 
    this.productId = this.$route.params.productId;
    this.getProduct(this.productId);
    this.getCart();
    this.getfavorite();
  },
  mounted() {
    // 
    this.toggleCart();
    this.toggleFavorite();
  },
};
</script>
